import React,{useEffect} from 'react';
import {updateTesting} from "src/actions/testingActions";
import ActivityHeader from '../ActivityHeader'
import store from 'src/store';

const testTitle = "kdds;vn;lvndsf;lvnds;lfvjn";

const testId = "zodhf[sdfjgfs";

const ActivityHeaderTest = () => {
    useEffect(()=>{
        store.dispatch(updateTesting({testTitle:testTitle,testid:testId}));
    },[]);
    return (
        <div>
            <div> </div>
            <ActivityHeader headerTitle={testTitle} id={testId}>
                <div id="testInclude">hello there</div>
            </ActivityHeader>
        </div>
    )
};
export default ActivityHeaderTest;
